import React from 'react';
import * as am4core from '@amcharts/amcharts4/core';
import * as am4charts from '@amcharts/amcharts4/charts';
// eslint-disable-next-line @typescript-eslint/camelcase
import am4themes_animated from '@amcharts/amcharts4/themes/animated';
import { slice } from 'lodash';

import { RatioData, RatioDataLineItem } from '../../reducers/state';

am4core.useTheme(am4themes_animated);

const SigmaAdder = 2;
const daysLookup = 20;

const compute = (arr: number[]) => {
	const mean = arr.reduce((acc, val) => acc + val, 0) / arr.length;
	return {
		mean,
		stddev: Math.sqrt(
			arr.reduce((acc, val) => acc.concat((val - mean) ** 2), [] as number[]).reduce((acc, val) => acc + val, 0) /
				(arr.length - 1),
		),
	};
};

interface ChartLine {
	value: number;
	mean: number;
	topCap: number;
	bottomCap: number;
	date: string;
}

const createData = (data: RatioDataLineItem[]) =>
	data.reduce((acc, each, index) => {
		if (index >= daysLookup) {
			const window = slice(data, index - daysLookup, index).map((e) => e.value);
			const { mean, stddev } = compute(window);
			acc.push({
				value: each.value,
				mean,
				topCap: mean - SigmaAdder * stddev,
				bottomCap: mean + SigmaAdder * stddev,
				date: each.date,
			});
		}
		return acc;
	}, [] as ChartLine[]);

const createSeries = (chart: am4charts.XYChart, valueField: string) => {
	const series = chart.series.push(new am4charts.LineSeries());
	series.dataFields.valueY = valueField;
	series.dataFields.dateX = 'date';
	return series;
};

const render = (chart: am4charts.XYChart, data: RatioDataLineItem[]) => {
	const chartData = createData(data);
	chart.data = chartData;

	// Create axes
	const dateAxis = chart.xAxes.push(new am4charts.DateAxis());
	dateAxis.renderer.minGridDistance = 60;
	dateAxis.tooltipDateFormat = 'dd-MMM-yyyy';
	chart.yAxes.push(new am4charts.ValueAxis());

	// Create series
	const seriesV = createSeries(chart, 'value');
	const seriesM = createSeries(chart, 'mean');
	const seriesT = createSeries(chart, 'topCap');
	const seriesB = createSeries(chart, 'bottomCap');

	chart.cursor = new am4charts.XYCursor();

	chart.scrollbarX = new am4core.Scrollbar();

	const info = chart.plotContainer.createChild(am4core.Container);
	info.width = 250;
	info.height = 100;
	info.x = 0;
	info.y = 0;
	info.padding(10, 10, 10, 10);
	info.background.fill = am4core.color('#000');
	info.background.fillOpacity = 0.1;
	info.layout = 'grid';

	// Create labels
	function createLabel(field: string, title: string) {
		const titleLabel = info.createChild(am4core.Label);
		titleLabel.text = title + ':';
		titleLabel.marginRight = 5;
		titleLabel.minWidth = 60;

		const valueLabel = info.createChild(am4core.Label);
		valueLabel.id = field;
		valueLabel.text = '-';
		valueLabel.minWidth = 50;
		valueLabel.marginRight = 30;
		valueLabel.fontWeight = 'bolder';
		return valueLabel;
	}

	const valueV = createLabel('value', 'Value');
	const valueM = createLabel('mean', 'Mean');
	const valueT = createLabel('topCap', 'Mean + 2Sigma');
	const valueB = createLabel('bottomCap', 'Mean - 2Sigma');

	function updateValues(dataItem: am4charts.XYSeriesDataItem | undefined, valueLable: am4core.Label) {
		valueLable.text = `${dataItem && dataItem.valueY.toFixed(3)}`;
	}

	chart.events.on('ready', () => {
		updateValues(seriesV.dataItems.last, valueV);
		updateValues(seriesM.dataItems.last, valueM);
		updateValues(seriesB.dataItems.last, valueB);
		updateValues(seriesT.dataItems.last, valueT);
	});

	chart.cursor.events.on('hidden', () => {
		updateValues(seriesV.dataItems.last, valueV);
		updateValues(seriesM.dataItems.last, valueM);
		updateValues(seriesB.dataItems.last, valueB);
		updateValues(seriesT.dataItems.last, valueT);
	});

	chart.cursor.events.on('cursorpositionchanged', () => {
		updateValues(
			dateAxis.getSeriesDataItem(seriesV, dateAxis.toAxisPosition(chart.cursor.xPosition), true),
			valueV,
		);
		updateValues(
			dateAxis.getSeriesDataItem(seriesM, dateAxis.toAxisPosition(chart.cursor.xPosition), true),
			valueM,
		);
		updateValues(
			dateAxis.getSeriesDataItem(seriesB, dateAxis.toAxisPosition(chart.cursor.xPosition), true),
			valueT,
		);
		updateValues(
			dateAxis.getSeriesDataItem(seriesT, dateAxis.toAxisPosition(chart.cursor.xPosition), true),
			valueB,
		);
	});
};

interface LineChartInterface {
	data: RatioData;
}

export class LineChart extends React.Component<LineChartInterface> {
	chart: am4charts.XYChart | undefined;
	componentDidMount() {
		const chart = am4core.create(this.props.data.id, am4charts.XYChart);

		render(chart, this.props.data.value);

		this.chart = chart;
	}

	componentWillUnmount() {
		if (this.chart) {
			this.chart.dispose();
		}
	}

	render() {
		return <div id={this.props.data.id} style={{ width: '500%', height: '60%' }} />;
	}
}
