import { isEmpty } from 'lodash';
import { RatioData, ManagementData } from '../reducers/state';
import { SheetsResult } from '../components/Maps';
const key = 'AIzaSyD7sMa9tWyeQ-9imPVlZr18vCGC5yr82Ks';

const ratioSheet = '1pmXJKMq2ApiaSnUmEIkdpXQWwLOZ3eaMuoK9wea3RN0';
const managementSheet = '13n92BF7dzExJ9Y4jf4S3Wg_9umwz26I_ZUY6JuIAOEk';

export class GoogleSheets {
	public static getAllCompanies(): Promise<string[]> {
		return GoogleSheets.makeQueries(ratioSheet, 'B2:B1848').then((v) =>
			v.values.reduce((acc: { [x: string]: any }, each: any[], index: string | number) => {
				acc[index] = each[0];
				return acc;
			}, []),
		);
	}

	public static async getCompanyData(dataId: string, company1Id: number, company2Id: number): Promise<RatioData> {
		const range1 = `C${company1Id + 2}:IU${company1Id + 2}`;
		const range2 = `C${company2Id + 2}:IU${company2Id + 2}`;

		const data1: any[] = await GoogleSheets.makeQueries(ratioSheet, range1).then((v) => v.values[0]);
		const data2: any[] = await GoogleSheets.makeQueries(ratioSheet, range2).then((v) => v.values[0]);

		const dates: any[] = await GoogleSheets.makeQueries(ratioSheet, `C1:IU1`).then((v) => v.values[0]);

		return {
			id: dataId,
			value: data1.map((each, index) => ({
				date: dates[index],
				value: parseInt(each) / parseInt(data2[index]),
			})),
		};
	}

	public static async getManagementInterviews(company1: string): Promise<ManagementData> {
		const data = await GoogleSheets.makeQueries(managementSheet, 'H2:I100000').then((v) => v.values.map(each => (isEmpty(each[0])? each[1]: each[0])));
		const companyMetadata = await GoogleSheets.makeQueries(managementSheet, 'B2:G100000').then((v) => v.values);
		const value = companyMetadata.reduce((acc, each, index) => {
			if(each[1] === company1){
				acc.push({
					date: each[0],
					media: each[2],
					mangName: each[3],
					desig: each[4],
					title: each[5],
					link: data[index],
				});
			}
			return acc;
		}, []);
		return {
			value,
			id: company1,
		}
	}

	public static async getDistrictInfo(): Promise<SheetsResult[]> {
		const data = await GoogleSheets.makeQueries('17Ows8Deu_MgpS5Ve-iZZyPNeuC2wBYPuFRU4pJOLQYA', 'A2:C734').then(v => v.values).then(e => e.map(x => ({ District: x[0], Zone_Type: x[2]}))) as SheetsResult[];
		return data;
	}

	private static makeQueries(sheet: string, range: string): Promise<{ values: any[] }> {
		return fetch(
			`https://sheets.googleapis.com/v4/spreadsheets/${sheet}/values/${range}?key=${key}`,
		).then((e) => e.json());
	}
}
