import { State } from '../reducers/state';
import { GoogleSheets } from '../services/google-sheets';
import { getCompany1ID, getCompany2ID, getCompany1, getCompany2 } from '../selectors';

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const getAllCompanies = (): any => (dispatch: any, getState: () => State) => {
	const state = getState();
	if (state.Ratio && !state.Ratio.allCompanies) {
		dispatch({ type: 'Start Loading' });
		GoogleSheets.getAllCompanies().then((companiesList) => {
			dispatch({ type: 'Get Companies', companiesList });
			dispatch({ type: 'Stop Loading' });
		});
	}
};

export const selectCompany2 = (company2: string) => (dispatch: any, getState: () => State) => {
	dispatch({ type: 'Company 2 Selected', company2 });
	dispatch(getData());
};

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export const getData = (): any => (dispatch: any, getState: () => State) => {
	const state = getState();

	const company1IdOrNull = getCompany1ID(state);
	const company2IdOrNull = getCompany2ID(state);

	const ratioDataId = `${getCompany1(state)}~${getCompany2(state)}`;
	if (typeof company1IdOrNull === 'number' && typeof company2IdOrNull === 'number') {
		dispatch({ type: 'Start Loading' });
		GoogleSheets.getCompanyData(ratioDataId, company1IdOrNull, company2IdOrNull).then((data) => {
			dispatch({
				data,
				type: 'Get Ratio Data',
			});
			dispatch({ type: 'Stop Loading' });
		});
	}
};
