import React from 'react';
import { connect } from 'react-redux';

import CompanySelector from './common/CompanySelector';
import { selectCompany } from '../actions/app';
import Loading from './common/Loading';
import { isAppLoading, getCompany1 } from '../selectors';
import { State } from '../reducers/state';

interface HomeProps {
	selectCompany: typeof selectCompany;
	isLoading: boolean;
	companySelected: string;
}
const Home: React.FC<HomeProps> = ({ isLoading, selectCompany, companySelected }) => (
	<Loading loading={isLoading} >
		<div style={{ marginLeft: '45%', marginTop: '10%', width: '15%' }}>
				<h2>Select Company</h2>
				<CompanySelector companySelectedCallBack={(e: any) => selectCompany(e.value)} value={companySelected} />
		</div>
	</Loading>
);

const mapStateToProps = (state: State) => ({
	isLoading: isAppLoading(state),
	companySelected: getCompany1(state),
});

export default connect(mapStateToProps, { selectCompany })(Home);
