import React from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";

import data from './2011_Dist';
import { GoogleSheets } from '../../services/google-sheets';

const COLORS = {
	'Red Zone': '#eb3434',
	'Orange Zone': '#ebab34',
	'Green Zone': '#34eb46',
}

type ZoneType = typeof COLORS;

export interface SheetsResult {
	District: string;
	Zone_Type: keyof ZoneType;
}

interface MapsProps {
}


const findColorForDistrict = (districData: SheetsResult[]) => (districtName: string) => {
	const res = districData.find(each => each.District === districtName);
	return res ? COLORS[res.Zone_Type] : COLORS['Red Zone'];
}

const render = (chart: am4maps.MapChart, districtData: SheetsResult[]) => {
	am4core.useTheme(am4themes_animated);
	
	const findColor = findColorForDistrict(districtData);
	const indexedData = { ...data, features: data.features.map((each, index) => 
		({...each, properties: {...each.properties, color: am4core.color(findColor(each.properties.DISTRICT))}, id: index})) 
	};

	// console.log(indexedData);
	chart.geodata = indexedData;
	
	chart.projection = new am4maps.projections.Miller();
	
	const polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());

	polygonSeries.useGeodata = true;

	const polygonTemplate = polygonSeries.mapPolygons.template;
	polygonTemplate.tooltipText = "{DISTRICT} - {ST_NM}";

	polygonTemplate.propertyFields.fill = "color";

	polygonTemplate.states.create("hover");

	chart.zoomControl = new am4maps.ZoomControl();
}


class Maps extends React.Component<MapsProps> {
	chart: am4maps.MapChart | undefined;
	componentDidMount() {
		GoogleSheets.getDistrictInfo().then(x => {
			this.chart = am4core.create("chartdiv", am4maps.MapChart);
			render(this.chart, x);
		});
	}

	componentWillUnmount() {
		if (this.chart) {
			this.chart.dispose();
		}
	}

	render() {
		return <div id="chartdiv" style={{ width: '500%', height: '700px' }} />;
	}
}

export default Maps;
