import { InitialState, Ratio } from './state';
import { ActionTypes } from '../actions/action-types';

const companies = (state: Ratio = InitialState.Ratio, action: ActionTypes): Ratio => {
	switch (action.type) {
		case 'Get Companies':
			return {
				...state,
				allCompanies: action.companiesList,
			};
		case 'Company 2 Selected':
			return {
				...state,
				company2: action.company2,
			};
		case 'Get Ratio Data':
			return {
				...state,
				data: action.data,
			};
		default:
			return state;
	}
};
export default companies;
