import { InitialState, Management } from './state';
import { ActionTypes } from '../actions/action-types';

const companies = (state: Management = InitialState.Management, action: ActionTypes): Management => {
	switch (action.type) {
		case 'Get Management Data':
			return {
				...state,
				data: action.data
			}
		default:
			return state;
	}
};
export default companies;
