import { InitialState, App } from './state';
import { ActionTypes } from '../actions/action-types';

const app = (state: App = InitialState.App, action: ActionTypes): App => {
	switch (action.type) {
		case 'Select Company':
			return {
				...state,
				companySelected: action.company,
			};
		case 'Start Loading':
			return {
				...state,
				loading: true,
			};
		case 'Stop Loading':
			return {
				...state,
				loading: false,
			};
		default:
			return state;
	}
};
export default app;
