import React from 'react';
import { connect } from 'react-redux';
import { Col } from 'react-bootstrap';
import CompanySelector from '../common/CompanySelector';
import Loading from '../common/Loading';
import { SidePanel } from '../common/Sidepanel';
import { State, ManagementData } from '../../reducers/state';
import { selectCompany } from '../../actions/app';
import { getAllCompanies, isAppLoading, getManagementData, getCompany1 } from '../../selectors';
import { getData } from '../../actions/Management';
import { InterviewList } from './InterviewList';

interface ManagementInterviewsProps {
	data: ManagementData | null;
	selectCompany: typeof selectCompany;
	isLoading: boolean;
	getData: typeof getData;
	company: string;
}

// const selectCallbackProxy = (callback, ...args) => ({value}) => callback(value, ...args);

class ManagementInterviews extends React.Component<ManagementInterviewsProps> {
	componentDidMount() {
		if (this.props.company) {
			this.props.getData();
		}
	}

	render() {
		const { selectCompany, isLoading, data, company } = this.props;
		return (
			<>
				<Col sm={2}>
					<SidePanel>
						<h3 style={{ fontFamily: 'sans-serif' }}> Management Interviews</h3>
						<div style={{ marginTop: '10%', fontSize: '12px' }}>
							<h6> Selected Company: </h6>
							<CompanySelector
								companySelectedCallBack={(e: any) => selectCompany(e.value)}
								value={company}
								isDisabled={true}
							/>
						</div>
					</SidePanel>
				</Col>
				<Col sm={14}>
					<Loading loading={isLoading}>{data && <InterviewList data={data} />}</Loading>
				</Col>
			</>
		);
	}
}

const mapStateToProps = (state: State) => ({
	allCompanies: getAllCompanies(state),
	isLoading: isAppLoading(state),
	data: getManagementData(state),
	company: getCompany1(state),
});

export default connect(mapStateToProps, { getData, selectCompany })(ManagementInterviews);
