import { createSelector } from 'reselect';
import { State, Ratio, App, Management } from '../reducers/state';

const getRatioState = (state: State) => state.Ratio;
const getAppState = (state: State) => state.App;
const getManagementState = (state: State) => state.Management;

export const getAllCompanies = createSelector(getRatioState, (ratio: Ratio) => ratio.allCompanies);
export const getCompany2 = createSelector(getRatioState, (ratio: Ratio) => ratio.company2);
export const isAppLoading = createSelector(getAppState, (app: App) => app.loading);
export const getCompany1 = createSelector(getAppState, (appState: App) => appState.companySelected);
export const getManagementData = createSelector(getManagementState, (appState: Management) => appState.data);

export const getCompany1ID = createSelector(
	getAllCompanies,
	getCompany1,
	(allCompanies, company1) => allCompanies && company1 && allCompanies.findIndex((each) => each === company1),
);

export const getCompany2ID = createSelector(
	getAllCompanies,
	getCompany2,
	(allCompanies, company2) => allCompanies && company2 && allCompanies.findIndex((each) => each === company2),
);

export const getRatioData = createSelector(getRatioState, (ratio: Ratio) => ratio.data);
