import React from 'react';
import { AgGridReact } from 'ag-grid-react';

import 'ag-grid-community/dist/styles/ag-grid.css';
import 'ag-grid-community/dist/styles/ag-theme-alpine.css';

import { ManagementData } from '../../reducers/state';

interface InterviewListProps {
	data: ManagementData
}

const dateComparator = (date1: string | number | Date, date2: string | number | Date) => {
	return new Date(date2).getTime() - new Date(date1).getTime();
}

const columnDefs = [
	{ headerName: "Date", field: "date", filter: true, sortable: true, resizable: true, comparator: dateComparator },
	{ headerName: "Media", field: "media", filter: true, sortable: true, resizable: true },
	{ headerName: "Management Name", field: "mangName", filter: true, sortable: true, resizable: true },
	{ headerName: "Designation", field: "desig", filter: true, sortable: true, resizable: true },
	{ headerName: "Title", field: "title", filter: true, sortable: true, resizable: true, width: 500 },
	{ headerName: "Link", field: "link", filter: true, sortable: true, resizable: true, cellRenderer: (params: { value: any; }) => `<a href=${params.value} target="_blank"> Link </a>` },
];

export const InterviewList: React.FC<InterviewListProps> = ({ data }) => (
	<div className="ag-theme-alpine" style={ {height: '100%', width: '1525px'} }>
	<AgGridReact
		columnDefs={columnDefs}
		rowData={data.value}>
	</AgGridReact>
  </div>
);
