import React from 'react';
import { connect } from 'react-redux';
import Select from 'react-select';

import { getAllCompanies } from '../../actions/Ratio';
import { State, Ratio } from '../../reducers/state';
import { getAllCompanies as getAllCompaniesFromState } from '../../selectors';

// 'AIzaSyD7sMa9tWyeQ-9imPVlZr18vCGC5yr82Ks'

interface CompanySelectorInterface {
	getAllCompanies: typeof getAllCompanies;
	allCompanies: Ratio['allCompanies'];
	companySelectedCallBack: any;
	value: string | null;
	isDisabled?: boolean;
}

const selectItemMaker = (item: string) => ({
	value: item,
	label: item,
});

const optionMaker = (allCompanies: Ratio['allCompanies']) =>
	allCompanies &&
	allCompanies.map(selectItemMaker);

class CompanySelector extends React.Component<CompanySelectorInterface> {
	componentDidMount() {
		this.props.getAllCompanies();
	}

	render() {
		return (
			<Select
				options={optionMaker(this.props.allCompanies) as any}
				onChange={this.props.companySelectedCallBack}
				isDisabled={this.props.isDisabled}
				value={this.props.value ? selectItemMaker(this.props.value): null}
			/>
		);
	}
}

const mapStateToProps = (state: State) => ({
	allCompanies: getAllCompaniesFromState(state),
});

export default connect(mapStateToProps, { getAllCompanies })(CompanySelector);
