import React from 'react';
import { connect } from 'react-redux';
import { Col } from 'react-bootstrap';
import CompanySelector from '../common/CompanySelector';
import Loading from '../common/Loading';
import { SidePanel } from '../common/Sidepanel';
import { State, RatioData } from '../../reducers/state';
import { selectCompany } from '../../actions/app';
import { selectCompany2 } from '../../actions/Ratio';
import { LineChart } from './LineChart';
import { getRatioData, getAllCompanies, getCompany1, getCompany2, isAppLoading } from '../../selectors';

interface RationChartProps {
	selectCompany1: typeof selectCompany;
	selectCompany2: any;
	data: RatioData | null;
	company1: string | null;
	company2: string | null;
	isLoading: boolean;
}

// const selectCallbackProxy = (callback, ...args) => ({value}) => callback(value, ...args);

const RatioChart: React.FC<RationChartProps> = ({ selectCompany1, selectCompany2, data, isLoading, company1, company2 }) => (
	<>
		<Col sm={2}>
			<SidePanel>
				<h3 style={{ fontFamily: 'sans-serif' }}> Ratio Chart </h3>
				<div style={{ marginTop: '10%', fontSize: '12px' }}>
					<h6> Selected Company Numerator: </h6>
					<CompanySelector companySelectedCallBack={(e: any) => selectCompany1(e.value)} value={company1} isDisabled={true} />
					<h6> Selected Company Denomenator: </h6>
					<CompanySelector companySelectedCallBack={(e: any) => selectCompany2(e.value)} value={company2} />
				</div>
			</SidePanel>
		</Col>
		<Col sm={14}>
			<Loading loading={isLoading} >
				{data ? <LineChart key={`${data.id}`} data={data} /> : <h2> Select Companies</h2>}
			</Loading>
		</Col>
	</>
);

const mapStateToProps = (state: State) => ({
	allCompanies: getAllCompanies(state),
	data: getRatioData(state),
	company1: getCompany1(state),
	company2: getCompany2(state),
	isLoading: isAppLoading(state),
});

export default connect(mapStateToProps, { selectCompany1: selectCompany, selectCompany2: selectCompany2 })(
	RatioChart,
);
