import React from 'react';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import { Navbar, Nav, Container, Row } from 'react-bootstrap';

import RatioChart from './components/Ration-Chart';
import Home from './components/Home';
import ManagementInterviews from './components/Management-Interviews';
import Maps from './components/Maps';

const pages = [
	{
		path: '/ratio-chart',
		link: `#/ratio-chart`,
		name: 'Ratio Chart',
		component: RatioChart,
	},
	{
		path: '/management-interviews',
		link: `#/management-interviews`,
		name: 'Management Interviews',
		component: ManagementInterviews,
	},
	{
		path: '/maps',
		link: `#/maps`,
		name: 'Maps',
		component: Maps,
	},
	{
		path: '/',
		link: `#/`,
		name: 'Home',
		component: Home,
	},
];

const App: React.FC = () => (
	<Container fluid>
		<Navbar fixed="top" bg="dark" variant="dark">
			<Navbar.Brand>Menu</Navbar.Brand>
			<Nav className="mr-auto">
				<Router basename="/">
					{pages.map((each, key) => (
						<Nav.Link key={key} href={each.link}>
							{each.name}
						</Nav.Link>
					))}
					<br />
				</Router>
			</Nav>
		</Navbar>
		<Row style={{ marginTop: '3%' }}>
			<Router basename="/">
				<Switch>
					{pages.map((each) => (
						<Route key={each.name} path={each.path} component={each.component} />
					))}
				</Switch>
			</Router>
		</Row>
	</Container>
);

export default App;
