interface Loadable {
	loading: boolean;
}
export interface App extends Loadable {
	companySelected: string;
}

export interface Ratio {
	allCompanies: string[] | null;
	company2: string | null;
	data: RatioData | null;
}

export interface RatioData {
	id: string;
	value: RatioDataLineItem[];
}

export interface RatioDataLineItem {
	date: string;
	value: number;
}

export interface Management {
	data: ManagementData | null;
}

export interface ManagementData {
	id: string;
	value: ManagementDataLineItem[];
}

export interface ManagementDataLineItem {
	date: string;
	media: string;
	mangName: string;
	desig: string;
	title: string;
	link: string;
}

export interface State {
	App: App;
	Ratio: Ratio;
	Management: Management;
}

export const InitialState: State = {
	App: {
		companySelected: '',
		loading: false,
	},
	Ratio: {
		allCompanies: null,
		company2: null,
		data: null,
	},
	Management: {
		data: null,
	},
};
